.home-container {
    max-width: 800px;
    margin: 20px auto;
}

.home-title {
    text-align: center;
    margin-bottom: 20px;
    font-size: 1.5rem;
    color: #5c67f2;
    ;

}

.select-container {
    margin-bottom: 15px;
}

.select-container select {
    width: 100%;
    padding: 8px;
    margin-bottom: 5px;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.select-container button {
    padding: 10px;
    background-color: #5c67f2;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
}

.select-container button:disabled {
    background-color: #b0b2d6;
    cursor: default;
}

.doctor-details-container {
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 15px;
    margin-bottom: 15px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    /* Added shadow for depth */
    background-color: white;
    /* Card background */
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Center content */
}

.doctor-profile {
    display: flex;
    align-items: center;
    width: 100%;
    /* Full width */
    margin-bottom: 15px;
    background-color: #f9f9f9;
    /* Light background for the profile section */
    border-radius: 8px;
    padding: 10px;
}

.doctor-image {
    width: 100px;
    /* Larger image */
    height: 100px;
    border-radius: 50%;
    margin-right: 15px;
    border: 3px solid #5c67f2;
    /* Border color similar to button background */
}


.doctor-info {
    flex: 1;
    /* Take up remaining space */
    display: flex;
    flex-direction: column;
    /* Stack info vertically */
}

.doctor-info h2 {
    margin-top: 0;
    margin-bottom: 5px;
    color: #333;
    /* Darker text for better readability */
}

.doctor-info p {
    margin: 5px 0;
    /* Spacing for readability */
}

.booking-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* Center booking items */
    width: 100%;
    /* Full width */
    margin-top: 10px;
    /* Space from profile */
}


