input[type="date"],
input[type="time"] {
    width: 100%;
    /* Full width inputs */
    padding: 10px;
    /* Larger padding for touch friendliness */
    margin-bottom: 10px;
    /* Space between inputs */
    border: 1px solid #ccc;
    border-radius: 4px;
}

.map-container {
    height: 400px;
    width: '100%';
    margin-bottom: 20px;
}

.search-bar {
    display: flex;
    justify-content: center;
    gap: 10px;
    margin-bottom: 20px;
}

.search-select {
    flex-grow: 1;
    /* Allow the selects to grow */
}

.search-button {
    padding: 10px 20px;
    background-color: #5c67f2;
    /* Or any color you prefer */
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

/* Adjust the max-width to manage space distribution */
@media (max-width: 768px) {
    .search-bar {
        flex-direction: column;
    }

    .search-select,
    .search-button {
        width: 100%;
        /* Full width on small screens */
    }
}

.date-picker-container,
.time-picker-container {
    margin-bottom: 10px;
}

input[type="date"],
input[type="time"] {
    border: 1px solid #ddd;
    padding: 8px;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}

/* Styling for the button */
button {
    background-color: #5c67f2;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
}



.overview-image {
    width: 100%;
    /* or any width you prefer */
    max-width: 600px;
    /* to ensure it doesn't get too large */
    display: block;
    /* block display for proper spacing */
    margin: 20px auto;
    /* centered and with some margin */
    border-radius: 8px;
    /* optional, for rounded corners */
}



